.category-single
  @include section-indent-margin

  &__swiper-container-thumb
    display: flex
    align-items: stretch
    min-width: 0

  &__wrapper
    display: flex
    gap: 0 36px

  &__item
    &:first-child
      width: calc(60% - 28px)
    &:last-child
      width: calc(40% - 8px)

  &__slide
    display: flex
    align-items: center
    justify-content: center
    width: 100%
    background: #FCF7F4
    height: auto

  &__slide, &__slide-thumb
    border-radius: 12px
    overflow: hidden
    position: relative

  &__swiper
    margin-bottom: 20px

  &__meta-inner
    position: absolute
    display: flex
    gap: 6px
    top: 30px
    left: 30px

  &__slide-link
    display: flex
    max-width: fit-content

  &__size-inner
    position: absolute
    display: flex
    align-items: center
    justify-content: center
    top: calc(50% - 36px)
    left: calc(50% - 36px)
    width: 72px
    height: 72px
    border-radius: 50%
    background: $title-second-color

  &__size-icon
    max-width: 32px
    max-height: 32px

  &__house-status
    position: absolute
    bottom: 0
    left: 50%
    transform: translateX(-50%)
    border-radius: 12px 12px 0 0
    background: rgba(255, 255, 255, 0.30)
    backdrop-filter: blur(10px)

  &__status-subtitle
    @include text-custom(16px,400,#fff)
    padding: 8px 15px
    text-transform: uppercase

  &__title
    @include section-title
    margin-bottom: 24px

  &__color
    text-transform: capitalize

  &__swiper-container-thumb
    position: relative

  &__slider-button
    @extend .reviews__slider-button

  &__price-month-inner
    display: flex
    align-items: center
    margin-bottom: 14px

  &__price-month
    @include text-custom(14px,400,rgba(83, 83, 83, .5))

  &__attention-trigger
    display: flex
    margin-left: 6px

  &__attention-icon
    max-width: 14px
    max-height: 14px

  &__attention-body
    position: fixed
    z-index: -1
    opacity: 0
    visibility: hidden

  &__price
    @include text-custom(30px,600,#B99D7B)
    margin-bottom: 18px

  &__price-inner
    display: flex
    gap: 0 42px
    margin-bottom: 24px

  &__price-sale-inner
    display: flex
    align-items: center

  &__old-price
    @include text-custom(20px,600,rgba(83, 83, 83, .5))
    margin-right: 14px

  &__sum-sale
    display: flex
    align-items: center
    justify-content: center
    border-radius: 60px
    background: $title-second-color
    @include text-custom(12px,400,#fff)
    padding: 8px 15px

  &__view-inner
    position: absolute
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    bottom: 48px
    left: 50%
    transform: translateX(-50%)

  &__view-icon
    max-width: 308px
    max-height: 28px

  &__view
    @include text-custom(16px,400,$title-second-color)

  &__features-inner
    display: flex
    align-items: center
    margin-bottom: 14px
    &--capital
      margin-bottom: 26px

  &__features-icon
    width: 30px
    height: 30px
    border-radius: 50%
    background-color: $title-second-color
    margin-right: 12px
