  &__text
    @include text-custom(14px)
    margin-bottom: 20px
    & span
      color: $title-second-color

  &__choose
    @include text-custom(12px,400,rgba(83, 83, 83, .5))
    margin-bottom: 10px

  &__custom-select
    margin-bottom: 14px
    &--material
      margin-bottom: 20px

  &__link-all
    display: block
    @include text-custom(12px,400,$title-second-color)
    margin-bottom: 14px
    text-decoration: underline

  &__button-inner
    display: flex
    align-items: center

  &__button-inner
    display: flex
    align-items: center
    justify-content: flex-start
    gap: 20px

  &__button
    @include button
    @include button-padding
    &--lending
      color: $title-second-color
      background: rgba(122, 160, 75, 0.10)









